import { AppUrl } from "../../../../constants";
import { onWindowEvent } from "../../../../utils";
import { TagembedAppUrl } from "../../../../actions/api";

const ErrorButtons = ({ editor, wall }) => {
    const isTagEmbedStatus = wall?.User?.userDatabaseType == "tagembed" || window.location.href.includes('tagembed')
    return <div className="tb_error_page_actions">
        <div className="tb_error_action_list">
            {!editor ? <a href={`${isTagEmbedStatus ? TagembedAppUrl : AppUrl}`} target={'_blank'} className={`tb_error_btn_primary`}>Access Now</a> : <>
                <button onClick={() => onWindowEvent(2)} className={`tb_error_btn_primary`} style={{ marginRight: '10px' }}>Add Another Feed</button>
                <button onClick={() => onWindowEvent(1)} className="loadDemoWall tb_error_btn_secondary">Show Demo Posts</button>
            </>}
        </div>
    </div>
}
export default ErrorButtons